html {
  scroll-behavior: smooth;
}
.App {
  height: 100vh;
  width: 80vw;
  display: flex;
  flex-direction: column;
}

a{
    text-decoration: none;
    color: white;
}

/* Header */

.header_container{
  
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; 
  width: 80vw;
  top: 8rem;
  position: relative;
  margin-top: 50px;
}
.header_container h1{
  position: relative;
  margin-left: 180px;
  font-size: 2rem;
  background-color: rgb(214, 137, 42);
  color: #1E6FD9;
  border: whitesmoke solid;
}
.header_container ul{
  list-style: none;
  margin-right: 140px;
  text-align: center;
}
.header_container ul li{
  display: inline-block;
  padding-right: 2rem;
  

}
.header_container ul li a{
  text-decoration: none;
  color: white;
  font-size: 2rem;
  font-weight: bolder;
  text-shadow: 2px 2px  rgb(214, 137, 42)
  
}
.burger-icon {
  display:none;
}

/* Home */
.land-flex{
  display: flex;
  flex-wrap: nowrap;
  background-color:#66669a42;
  flex-direction: column;
  justify-content: center;
  /* background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRQHOKi9EXXkhkMm5f1tFYwFSX8O9j58KirWQ&usqp=CAU"); */
  /* background-size: cover; */
  width: 100%;
  margin-top: 30px;
  /* right: 0px; */
  
}
.home_container{
  display: flex;
  flex-direction: row;
  justify-content:space-around;
  position: relative;
  top: 5rem;
  width: 90vw;
  margin-bottom: 170px;
  /* background-color: lightgrey; */
}

.home_container .home_head_wrapper{
  position: relative;
  top:2rem;
  display: flex;
  flex-direction: column;
  width: 40vw;
}
.home_container .home_head_wrapper h1{
  font-size: 4.5rem;
  font-family: "Montserrat";
}
.home_container .home_head_wrapper h1 span{
  color:#1E6FD9;
}
.home_container .home_head_wrapper p{
  position: relative;
  top:-4rem;
  font-size: 2rem;
  opacity: 0.4;
}
.home_container .image_container img{
  
  position: relative;
  height: 26rem;
  width: 20rem;
  top:5rem;
  border-style: solid;
  border-width: 5px;
  border-top: 0px;
  border-left: 0px;
  border-color: #1E6FD9;
  margin-right: 120px;
  
}
/* LANDING PAGE SCROLL ICONE*/

.Landing-container{
  position: absolute;
  width: 50px;
  height: 50px;
  margin-left: 550px;
  margin-top: 400px;
  /* background-color: yellowgreen; */
 
 
}
.chevron {
  /* position: absolute; */
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: ' ';
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
  margin-top: 0px;
}

.chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;

  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

.text {
  display: block;
  margin-top: 75px;
  margin-left: -30px;
  font-family: "Helvetica Neue", "Helvetica", Arial, sans-serif;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: .25;
  animation: pulse 2s linear alternate infinite;
}

@keyframes pulse {
  to {
    opacity: 1;
  }
}


/* Work */
.work_container{
  background-color: #66669a42;;
    display: flex;
    flex-direction: column;
    position: relative;
    top: 18rem;
    width: 80vw;
    justify-content: center;
}
.work_container h1{
